import React from "react";
import styled from "styled-components";
import { useNavigate } from "react-router-dom";
import { auth } from "../shared/firebase";
import { signInWithEmailAndPassword } from "firebase/auth"

const Login = () => {

    const navigate = useNavigate();

    //  
    const [id_check, setId] = React.useState("");
    const [pwd_check, setPwd] = React.useState("");

    // 
    const id_ref = React.useRef("");
    const password_ref = React.useRef("");

    const loginFB = async () => {
        //  catch .
        try {
            const user = await signInWithEmailAndPassword(
                auth,
                id_ref.current.value,
                password_ref.current.value
            );
            console.log(user);

            if (user.operationType === "signIn") {
                window.alert("");
                navigate("/");
            }
        } catch (e) {
            window.alert(" ");
        }
    }


    return (
        <WordDiv>
            <Subtitle></Subtitle>
            <CardStyle>
                <IdPwdTitle></IdPwdTitle>
                <Input>
                    <input type="text" ref={id_ref} style={{ width: "300px" }}
                        onChange={(e) => {
                            setId(e.target.value);
                        }}
                    />
                </Input>
                <IdPwdTitle></IdPwdTitle>
                <Input>
                    <input type="password" ref={password_ref} style={{ width: "300px" }}
                        onChange={(e) => {
                            setPwd(e.target.value);
                        }}
                    />
                </Input>
                <LoginBtn disabled={id_check === "" || pwd_check === ""
                    ? true : false} onClick={loginFB}></LoginBtn>
            </CardStyle>
        </WordDiv>
    );
};

const WordDiv = styled.div`
max-width: 450px;
display: flex;
flex-direction: column;
align-items: center;
justify-content: center;
margin: 100px auto;
`;

const Subtitle = styled.div`
    width: 325px;
    height: 50px;
    font-size: 25px;
    font-weight: 600;
    text-align: center;
    margin-top: 25px;
    color: darkviolet;
    text-decoration: underline blueviolet;
`;

const CardStyle = styled.div`
margin: 10px;
padding: 10px;
border: 20px solid lightskyblue;
border-radius: 10px;
background-color: #cfffaf;
`;

const IdPwdTitle = styled.h5`
text-decoration: underline;
margin: 0px auto;
`;

const Input = styled.div`
  width: 300px;
  height: 50px;
  margin-top: 10px;
  margin-bottom: 10px;
  padding: 16px auto;
  display: flex;
  & input{
    border: 1px solid #888;
    width: 70%;
  }
  
  & input:focus {
    outline: none;
    border: 3px solid #888;
  }
`;

const LoginBtn = styled.button`
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 25px;
    margin-bottom: 20px;
    width: 100%;
    height: 50px;
    background-color: #673ab7;
    color: white;
    font-size: 20px;
    font-weight: bold;

`;
export default Login;